	module.exports = {"data":{"wordpressPost":{"title":"Victoria Beckham Has Created A Powerful New Collection For Estee Lauder","content":"<p>Victoria Beckham has released a second makeup collection with Estee Lauder and it&#8217;s a powerful one. How exactly? It&#8217;s flattering and new and well, thoroughly modern, all of which Victoria believes will make the women who wear it feel both confident and strong.</p>\n<p>The one-time Spice Girl and now thoroughly established fashion designer told Stella magazine: &#8220;I was at the airport looking at the makeup in duty-free and I thought, &#8216;Everything looks the same&#8217;. <strong>For me it&#8217;s not about copying what everyone else is doing, it&#8217;s about creating makeup that women feel proud to own. Ultimately, it&#8217;s about empowering women.&#8221;</strong></p>\n<p>Victoria, 43, also revealed that she tests all of the products she puts her name to and also, gets advice from her husband super star soccer player David Beckham. She also examines photos of herself to decide what she likes and what she would prefer not to include within her collection.</p>\n<figure id=\"attachment_2900\" style=\"width: 225px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-2900\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19192607/VBxEL_Autumn-Winter-17_Paris-Look-on-Black_Global_Use-from-August-1st-to-December-2017_FINAL-225x300.jpg\" alt=\"\" width=\"225\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19192607/VBxEL_Autumn-Winter-17_Paris-Look-on-Black_Global_Use-from-August-1st-to-December-2017_FINAL-225x300.jpg 225w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19192607/VBxEL_Autumn-Winter-17_Paris-Look-on-Black_Global_Use-from-August-1st-to-December-2017_FINAL-768x1022.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19192607/VBxEL_Autumn-Winter-17_Paris-Look-on-Black_Global_Use-from-August-1st-to-December-2017_FINAL-769x1024.jpg 769w\" sizes=\"(max-width: 225px) 100vw, 225px\" /><figcaption class=\"wp-caption-text\">The much-awaited collection &#8211; bold, strong and beautiful, like the women who will wear it.</figcaption></figure>\n<p>&nbsp;</p>\n<p>This particular collection is close to Victoria&#8217;s heart and she says she hopes it will make women everywhere feel &#8216;beautiful and confident&#8217;. It certainly looks like it will. The colour palette is flattering and modern yet also daring. Just like the first collection, the textures are immensely high in quality, the pigments rich and the colour pay-off intense. Plus, they go on easy and even more importantly, stay put.</p>\n<p>Victoria loves all of the pieces but admits to being especially smitten with the nude lipstick.</p>\n<p>&#8220;I&#8217;m obsessed with nude lipsticks and the new matte lipstick formula is absolute perfection,&#8221; she says.</p>\n<p>&#8220;I learn an enormous amount by seeing pictures of myself and in photos the new shades read as nude, but in practical terms you can get a really great lip shape and it doesn&#8217;t bleed. I put this lipstick on at seven o&#8217;clock this morning &#8211; I&#8217;ve eaten my breakfast, my lunch and it&#8217;s still in place.&#8221;</p>\n<figure id=\"attachment_2903\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-2903\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19194201/VBxEL_Double-Page-Advertising-Visual-300x204.jpg\" alt=\"\" width=\"300\" height=\"204\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19194201/VBxEL_Double-Page-Advertising-Visual-300x204.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19194201/VBxEL_Double-Page-Advertising-Visual-768x522.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19194201/VBxEL_Double-Page-Advertising-Visual-1024x696.jpg 1024w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19194201/VBxEL_Double-Page-Advertising-Visual.jpg 1152w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">The sophisticated advertising is sure to attract women the world over.</figcaption></figure>\n<p>&nbsp;</p>\n<p>The new range, which is a limited edition collection, will be available from September. To get advanced warning of when it&#8217;s coming follow <a href=\"https://www.instagram.com/victoriabeckham/\">@VictoriaBeckham</a> and also <a href=\"https://www.instagram.com/esteelauder/\">@EsteeLauder</a> on Instgram.</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/19185621/VBxEL_Official-Campaign-Visual.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"0b8fea52-2ab2-5894-bc92-e50e6f0d307b"}}

